import '../Cart/style.css'

import React from 'react';

import { Link } from 'react-router-dom'

import CardInCart from '../../components/CardInCart';

import { useDispatch, useSelector } from 'react-redux';

import { selectCart } from '../../redux/cart/selectors';
import { clearItems } from '../../redux/cart/slice';

import { CartItemsProps } from '../../components/CardInCart';





const Cart: React.FC = () => {




   const { items, totalPrice } = useSelector(selectCart)


   const dispatch = useDispatch();




   return (


      <main className="cart">
         <div className="cart__container container">
            <div className="cart__title-block tilte-block-cart">
               <div className="tilte-block-cart__left">
                  <img className="tilte-block-cart__cart-icon" src="./img/cart/cart-icon.svg" alt="" />
                  <h1 className="tilte-block-cart__title">Корзина</h1>
               </div>
               <div className="tilte-block-cart__right">
                  <img className="tilte-block-cart__trash-icon" src="./img/cart/trash-icon.png" alt="" />
                  <a onClick={(e) => { dispatch(clearItems()); e.preventDefault() }} className="tilte-block-cart__link" href="">Очистить корзину</a>
               </div>
            </div>
            <div className="cart__items">

               {items.map((obj, index: number) => (
                  <CardInCart key={index} items={obj} />
               ))}



            </div>
            <div className="cart__final final-cart">
               <div className="final-cart__left">
                  <h4 className="final-cart__text">Всего пицц:</h4>

                  <span className="final-cart__number">{items.length > 0 ? items.map(i => i.count).reduce((a: number, b: number) => a + b) : 0} шт.</span>
               </div>
               <div className="final-cart__right">
                  <h4 className="final-cart__text">Сумма заказа:</h4>
                  <span className="final-cart__price">{totalPrice} ₽</span>
               </div>
            </div>
            <div className="cart__btn-block">
               <Link to='/' className="cart__return cart-btn">
                  <svg width="8" height="14" viewBox="0 0 8 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                     <path d="M7 13L1 6.93015L6.86175 1" stroke="#D3D3D3" strokeWidth="1.5" strokeLinecap="round"
                        strokeLinejoin="round" />
                  </svg>
                  Вернуться назад
               </Link>
               <button className="cart__purchaise cart-btn">
                  Оплатить сейчас
               </button>
            </div>
         </div>
      </main>



   );
}

export default Cart;